
export const getCountWord = (count: number, type: 'days' | 'hotels'): string => {
    if (type === 'days') {
        if (count === 1) return '';
        if (count >= 2 && count <= 4) return '';
        return '';
    }
    if (type === 'hotels') {
        if (count === 1) return '';
        if (count >= 2 && count <= 4) return '';
        return '';
    }
    return '';
};